import React, { Component } from 'react';
import './danh_sach_tinh.css'
import { toast } from 'react-toastify';
import sampleSize  from 'lodash.samplesize';
import {
  action_update_data_like_setup,
  get_data_like_setup,
  // clear_catche_by_id
} from '../axios'
export default class Danh_sach_tinh extends Component {
  constructor(props) {
    super(props)
      this.state = {
          checked:'',
          text_check:'',
          selected:-1,
          input_add_tinh:'',
          input_add_huyen:'',
          data:[
            // {
            //   id:'sda',
            //   ten_tinh:"Gia Lai",
            //   huyen_xa:[
            //     "Th x Ayun Pa",
            //     "Th x An Kh",
            //     "Ph Thin"
            //   ],
            //   cua_hang:[
            //     {
            //       ten:"Ni Tht An Bnh, chuyn key_word",
            //       sp:"ging st",
            //       lien_he:"0963226771",
            //       dia_chi:[
            //         "54 Ng Quyn, th x Ayun Pa, Gia Lai",
            //         "60 Ng c K, Th X sng b, Gia Lai"
            //       ]
            //     },
            //     {
            //       ten:"Ni Tht An Bnh, chuyn key_word",
            //       sp:"ging st",
            //       lien_he:"0963226771",
            //       dia_chi:[
            //         "54 Ng Quyn, th x Ayun Pa, Gia Lai",
            //         "60 Ng c K, Th X sng b, Gia Lai"
            //       ]
            //     },
            //   ]
            // },
          ]
      }
    }

  async componentDidMount() {
    let text_check=await window.localStorage.getItem("selected_tinh");
    text_check=text_check==null?"":text_check;
    let a= await get_data_like_setup('data_tinh_viet_nam')
    if(a.status){
      this.setState({data:a.data,text_check:text_check})
    }
  }

  render() {
    let {data,input_add_tinh,input_add_huyen,selected,text_check,checked}=this.state
    return (
      <React.Fragment>
        <div className='container mt-50'>

         <div className='row'>
          <div className='col-4'>
            <div className="input-group mb-3 re">
              <input type="text" className="form-control" placeholder="Nhp tnh" aria-label="Recipient's username" aria-describedby="basic-addon2"
                value={input_add_tinh}
                onChange={(e)=>{
                  this.setState({input_add_tinh:e.target.value})
                }}
              />
              <div className="input-group-append">
                <button type="button" className="btn btn-primary btn-primaryx"
                  onClick={()=>{
                    let {data}=this.state;
                    if(input_add_tinh.length>3){
                      data.unshift({
                        id:this.makeid(3),
                        ten_tinh:input_add_tinh,
                        huyen_xa:[],
                        cua_hang:[]
                      })
                      this.setState({data:data,input_add_tinh:""})
                      toast.success(' thm.',{theme: "colored"});
                    }else{
                      toast.error('Tn tnh phi ln hn 3 k t.',{theme: "colored"});
                    }
                  }}
                ><i className="fa-solid fa-plus"></i><span className="hgfs">Thm tnh</span></button>
              </div>
              <div className='zias'>
                <button className='buzz buhh ml-3'
                    onClick={()=>{
                      navigator.clipboard.writeText('key_word');
                      toast.success(` copy: "key_word"`, { theme: "colored" })
                    }}
                  >key_word<i className="fa-regular fa-clone ml-3"></i></button>
              </div>
            </div>
          </div>
         </div>

         {/*  */}

        <div className='wrapxs'>
          {
            data.map((e,i)=>{
              let chek=text_check.search(e.id+",")==-1?false:true;
              return(
                   <div className='tagxs' key={e.id}>
                      <div className={chek?'checkxs activesel':'checkxs'}
                        onClick={()=>{
                          let {text_check}=this.state;
                          if(text_check.search(e.id+",")==-1){
                            text_check+=e.id+",";
                          }else{
                            text_check=text_check.replace(e.id+",", "");
                          }
                          this.setState({text_check:text_check,input_add_huyen:'',input_add_tinh:''});
                          window.localStorage.setItem("selected_tinh",text_check);
                        }}
                      >
                        <span className="xxy">{i+1}</span>
                      </div>
                      <div className={selected==e.id?"tillexs activexx":"tillexs"}
                        onClick={()=>{
                          if(selected==e.id){
                            this.setState({selected:''})
                          }else{
                            this.setState({selected:e.id})
                          }
                          
                        }}
                      >{e.ten_tinh}</div>
                      <div className="xxxs brsx" 
                        onClick={()=>{
                          if(window.confirm(`Xc nhn xa : "${e.ten_tinh}"`)){
                            let {data}=this.state;
                            data.splice(i,1);
                            this.setState({data:data})
                          }
                        }}
                      ><i className="fa-sharp fa-solid fa-trash"></i></div>
                    </div>
              )
            })
          }
        </div>
        {
      data.map((e,i)=>{
        return <div key={e.id}>
          {selected==e.id&&<div className='row wrap-s csxx' key={i} style={{backgroundColor:"#a5a4a3"}}>
            <div className='col-6'>
                <div className="input-group mb-3">
                  <span className="input-group-text">Tn (tnh):</span>
                  <input type="text" className="form-control" placeholder="Gia Lai" aria-label="Username" aria-describedby="basic-addon1" 
                    value={e.ten_tinh}
                    onChange={(e)=>{
                      let {data}=this.state;
                      data[i].ten_tinh=e.target.value
                      this.setState({data:data})
                    }}
                  />
                </div>
            </div>
            <div className='col-12'>
              <h4>*Tn th x, huyn,..vv. (Khu vc trc thuc nh hn)</h4>
            </div>
            <div className='col-5'>
              <div className="input-group mb-3">
                <input type="text" className="form-control" placeholder="Nhp huyn, x" aria-label="Recipient's username" aria-describedby="basic-addon2"
                    value={input_add_huyen}
                    onChange={(e)=>{
                      this.setState({input_add_huyen:e.target.value})
                    }}
                />
                <div className="input-group-append">
                  <button type="button" className="btn btn-primary btn-primaryx"
                    onClick={()=>{
                      let {data}=this.state
                      if(input_add_huyen.length>3){
                        data[i].huyen_xa.unshift(input_add_huyen);
                        this.setState({data:data,input_add_huyen:""})
                        toast.success(`Thm thnh thnh cng.`,{theme: "colored"})
                      }else{
                        toast.error('Tn huyn, x phi ln hn 3 k t.',{theme: "colored"});
                      }
                    }}
                  ><i className="fa-solid fa-plus"></i><span className="hgfs">Thm th x, huyn</span></button>
                </div>
              </div>
            </div>
              {/*  */}
            <div className='wrapxs' style={{backgroundColor:"antiquewhite"}}>
              {
                e.huyen_xa.map((huyen,j)=>{
                  return <div className='tagxs' key={j}>
                  <div className={checked==huyen?'checkxs echsasxz':'checkxs'}
                    onClick={()=>{
                        this.setState({checked:huyen}) 
                    }}
                  >
                    <span className="xxy">{j+1}</span>
                  </div>
                  <div className='tillexs'
                    onClick={()=>{
                        navigator.clipboard.writeText(huyen);
                        toast.success(` copy: "${huyen}"`, { theme: "colored" })
                        this.setState({checked:huyen}) 
                    }}
                  ><b>{huyen}</b></div>
                  <div className="xxxs"
                    onClick={()=>{
                      if(window.confirm(`Xc nhn xa: "${huyen}"`)){
                        let {data}=this.state;
                        data[i].huyen_xa.splice(j,1);
                        this.setState({data:data,input_add_huyen:""})
                      }
                        navigator.clipboard.writeText(huyen);
                        toast.success(` copy: "${huyen}"`, { theme: "colored" })
                    }}
                  ><i className="fa-sharp fa-solid fa-trash"></i></div>
                </div>
                })
              }
          
            </div>
            <div className="input-group-append">
                  <button type="button" className="btn btn-primary btn-primaryx"
                    onClick={()=>{
                      let {data}=this.state;
                      data[i].cua_hang.unshift({
                        ten:"",
                        sp:"key_word",
                        lien_he:"",
                        dia_chi:["",""]
                      },)
                      this.setState({data:data})
                    }}
                  ><i className="fa-solid fa-plus"></i><span className="hgfs">Thm a ch ca hng  khu vc ny</span></button>
                </div>
            <table className="table table-hover table-striped">
              <thead>
                <tr>
                  <th scope="col" style={{width:'1%'}}>#</th>
                  <th scope="col" style={{width:'50%'}}>Ca hng bn</th>
                  <th scope="col" style={{width:'18%'}}>Sn phm</th>
                  <th scope="col" style={{width:'20%'}}>Lin h</th>
                </tr>
              </thead>
              <tbody>
                {
                  e.cua_hang.map((shop,j)=>{
                    return <tr key={j}>
                    <td scope="row">{j+1}
                      <div className="xxxs"
                        onClick={()=>{
                          if(window.confirm(`Xc nhn xa : "${shop.ten}"`)){
                            let {data}=this.state;
                            data[i].cua_hang.splice(j,1);
                            this.setState({data:data})
                          }
                        }}
                      ><i className="fa-sharp fa-solid fa-trash"></i></div>
                    </td>
                    <td scope="row">
                      <div className="input-group ">
                        <span className="input-group-text">Tn ni tht:</span>
                        <input type="text" className="form-control" placeholder="Ni tht An Bnh chuyn [key_word]" aria-label="Username" aria-describedby="basic-addon1" 
                          value={shop.ten}
                          onChange={(e)=>{
                            let {data}=this.state;
                            data[i].cua_hang[j].ten=e.target.value;
                            this.setState({data:data})
                          }}
                          
                        />
                        <i className="fa-solid fa-robot renderzd"
                          onClick={()=>{
                            let {data}=this.state;
                            data[i].cua_hang[j].ten=shop.ten+sampleSize([
                              ', chuyn key_word',
                              ' bn key_word',
                              ' key_word',
                              ' - key_word',
                              ' - [key_word]',
                              ' - (key_word)',
                              ' - c bn key_word',
                              ' - cung cp key_word',
                              ' - phn phi key_word',
                            ],1)[0];
                            this.setState({data:data})
                          }}
                        ></i>
                      </div>
                      <div className="input-group">
                        <span className="input-group-text">a ch 1:</span>
                        <input type="text" className="form-control" placeholder="..." aria-label="Username" aria-describedby="basic-addon1" 
                          value={shop.dia_chi[0]}
                          onChange={(e)=>{
                            let {data}=this.state;
                            data[i].cua_hang[j].dia_chi[0]=e.target.value;
                            this.setState({data:data})
                          }}
                          
                        />
                      </div>
                      <div className="input-group mb-3">
                        <span className="input-group-text">a ch 2:</span>
                        <input type="text" className="form-control" placeholder="..." aria-label="Username" aria-describedby="basic-addon1" 
                          value={shop.dia_chi[1]}
                          onChange={(e)=>{
                            let {data}=this.state;
                            data[i].cua_hang[j].dia_chi[1]=e.target.value;
                            this.setState({data:data})
                          }}
                          
                        />
                      </div>
                    </td>
                    <td scope="row">
                      <input type="text" className="form-control" placeholder="Ging st" aria-label="Username" aria-describedby="basic-addon1" 
                            value={shop.sp}
                            onChange={(e)=>{
                              let {data}=this.state;
                              data[i].cua_hang[j].sp=e.target.value;
                              this.setState({data:data})
                            }}
                            
                          />
                    </td>
                    <td scope="row">
                      <input type="text" className="form-control" placeholder="0963226771" aria-label="Username" aria-describedby="basic-addon1" 
                            value={shop.lien_he}
                            onChange={(e)=>{
                              let {data}=this.state;
                              data[i].cua_hang[j].lien_he=e.target.value;
                              this.setState({data:data})
                            }}
                            
                          />
                    </td>
                  </tr>
                  })
                }
                {/* <tr>
                  <td scope="row">1</td>
                  <td scope="row">
                    <div className="input-group">
                      <span className="input-group-text">Tn ni tht:</span>
                      <input type="text" className="form-control" placeholder="Ni tht An Bnh chuyn [key_word]" aria-label="Username" aria-describedby="basic-addon1" 
                        // value={selected_page.title}
                        // onChange={(e)=>{
                        //   let {selected_page}=this.state;
                        //   selected_page.title=e.target.value;
                        //   this.setState({selected_page:selected_page})
                        // }}
                        
                      />
                    </div>
                    <div className="input-group">
                      <span className="input-group-text">a ch 1:</span>
                      <input type="text" className="form-control" placeholder="..." aria-label="Username" aria-describedby="basic-addon1" 
                        // value={selected_page.title}
                        // onChange={(e)=>{
                        //   let {selected_page}=this.state;
                        //   selected_page.title=e.target.value;
                        //   this.setState({selected_page:selected_page})
                        // }}
                        
                      />
                    </div>
                    <div className="input-group mb-3">
                      <span className="input-group-text">a ch 2:</span>
                      <input type="text" className="form-control" placeholder="..." aria-label="Username" aria-describedby="basic-addon1" 
                        // value={selected_page.title}
                        // onChange={(e)=>{
                        //   let {selected_page}=this.state;
                        //   selected_page.title=e.target.value;
                        //   this.setState({selected_page:selected_page})
                        // }}
                        
                      />
                    </div>
                  </td>
                  <td scope="row">
                    <input type="text" className="form-control" placeholder="Ging st" aria-label="Username" aria-describedby="basic-addon1" 
                          // value={selected_page.title}
                          // onChange={(e)=>{
                          //   let {selected_page}=this.state;
                          //   selected_page.title=e.target.value;
                          //   this.setState({selected_page:selected_page})
                          // }}
                          
                        />
                  </td>
                  <td scope="row">
                    <input type="text" className="form-control" placeholder="0963226771" aria-label="Username" aria-describedby="basic-addon1" 
                          // value={selected_page.title}
                          // onChange={(e)=>{
                          //   let {selected_page}=this.state;
                          //   selected_page.title=e.target.value;
                          //   this.setState({selected_page:selected_page})
                          // }}
                          
                        />
                  </td>
                </tr> */}
              </tbody>
            </table>
          </div>}
        </div>
      })}
       
 

          </div>
 
        <div className="footer-edit">
            <button type="button" className="btn btn-primary"
              onClick={async()=>{
                let {data}=this.state;
                let a = await action_update_data_like_setup({
                  name:'data_tinh_viet_nam',
                  value:JSON.stringify(data)
                })
                if(a.status){
                  toast.success(`Cp nht thnh cng.`,{theme: "colored"})
                }else{
                  toast.error('Li ri bn i!.',{theme: "colored"})
                }
              }}
            >Cp nht bi vit</button>
        </div>
      </React.Fragment>
    );
  }
  makeid=(length)=> {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  }
}

