const axios = require('axios');
const { toast } =require ('react-toastify');
var url_home=window.data;
async function fs_axios_get(url,return_err){ // return_err => ARRAY ~~ OBJECT
    let data= await axios.get(url)
    .then(function (response) {
        console.log(" ~ file: axios.js:9 ~ response.data", response.data)
        return response.data
    })
    .catch(function (error) {
        toast.error('ERROR!',{theme: "colored"})
        console.log(error)
        // handle error
        if(return_err=='ARRAY') return [];
        return {};
    })
    return data;
}
let url_create_or_edit_page=window.home_url+'/wp-content/themes/theme_meme/templates/ajax/page/create_edit_page.php';
export async function action_create_or_edit_page(data){
    let data_send=new FormData();
    Object.keys(data).forEach(function(key) {
            data_send.append(key,data[key]);
    });
    // //
    let response= axios.post(url_create_or_edit_page, 
        data_send
    )
    .then(function (response) {
        console.log(" ~ file: axios.js:15 ~ response", response.data)
        return response.data;
    })
    .catch(function (error) {
        console.log(" ~ file: axios.js ~ line 44 ~ action_create_or_edit_post ~ error", error)
        return {}
    })
    return response;
}
