const url_upload=window.home_url+'/wp-content/themes/theme_meme/templates/ajax/media/upload_core.php';
export async function upload_core(data){
    let formData = new FormData();
    if(data.length>0){
        for(let i=0;i<data.length;i++){
            formData.append(i,data[i]);
        }
    }
    //
    let response= axios.post(url_upload, 
        formData
    )
    .then(function (response) {
        if(response.data.length>0){
            return response.data
        }else{
            return []
        }
    })
    .catch(function (error) {
        console.log(" ~ file: axios.js ~ line 97 ~ action_create_or_edit_post ~ error", error)
        return []
    })
    return response;
}
