import React, { Component } from 'react';
import './setup.css'
import { toast } from 'react-toastify';
import Media_img from '../Editer/Add_media';
import {
  action_update_data_like_setup,
  get_data_like_setup
} from '../axios'
export default class Setup extends Component {
  constructor(props) {
    super(props)
    this.state = {
      is_open_media:false,
      img_ref:"",
      data:{
        mini_icon:"",
        background_header:"",
        logo_website:"",
        loi_chao_mung:"",
        sdt_zalo:"",
        sdt_hotline:"",
        link_fb:"",
        dia_chi:"",
        about_footer:[],
        jobs_footer:[],
        privacy_footer:[],
        design_by:"",
        code_header:"",
        code_body:"",
        code_footer:"",
        ads_modal:"",
        ads_detail:"",
        ads_more:[],
        // time_catche:0
      }
    }
  }
  async componentDidMount() {
    let a= await get_data_like_setup('setup_meme_theme')
    if(a.status){
      this.setState({data:a.data})
    }
 
  }

  render() {
    let {is_open_media,data}=this.state
    return (
      <React.Fragment>
        <div className='container mt-50'>
          <div className='row mb-42'>
            <div className='col-4'>

              <div className='w-1'>
                <div className='head-1'>
                  Icon mini cho trang web:
                </div>
                <div className='body-1'>
                  <button className='buzz' 
                    onClick={()=>{
                      this.setState({
                        is_open_media:true,
                        img_ref:"mini_icon"
                      })
                    }}
                  >
                    <i className="fa-solid fa-photo-film"></i> <span>Add Media</span>
                  </button>
                  <img src={data.mini_icon} width="50px" className='ml-3' />
                </div>
              </div>
            </div>

            <div className='col-4'>
              <div className='w-1'>
                <div className='head-1'>
                  Chn background cho header:
                </div>
                <div className='body-1'>
                  <button className='buzz' 
                    onClick={()=>{
                      this.setState({
                        is_open_media:true,
                        img_ref:"background_header"
                      })
                    }}
                  >
                    <i className="fa-solid fa-photo-film"></i> <span>Add Media</span>
                  </button>
                  <img src={data.background_header} width="50px" className='ml-3' />
                </div>
              </div>
            </div>

            <div className='col-4'>
              <div className='w-1'>
                <div className='head-1'>
                Logo trang web:
                </div>
                <div className='body-1'>
                  <button className='buzz'
                    onClick={()=>{
                      this.setState({
                        is_open_media:true,
                        img_ref:"logo_website"
                      })
                    }}
                  >
                    <i className="fa-solid fa-photo-film"></i> <span>Add Media</span>
                  </button>
                  <img src={data.logo_website} width="50px" className='ml-3' />
                </div>
              </div>
            </div>

          </div>

          <div className='row mb-42'>
            <div className='col-8'>
              <div className="input-group mb-3">
                <span className="input-group-text">Li cho mng:</span>
                <input type="text" className="form-control" placeholder="Cho mng bn n vi ni Tht Cofa.vn" aria-label="Username" aria-describedby="basic-addon1" 
                  value={data.loi_chao_mung}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.loi_chao_mung=e.target.value;
                    this.setState({data:data})
                  }}
                />
              </div>
            </div>
          </div>

          <div className='row mb-42'>
            <div className='col-8'>
              <div className="input-group mb-3">
                <span className="input-group-text">Lin h s in thoi zalo:</span>
                <input type="text" className="form-control" placeholder="0933226771" aria-label="Username" aria-describedby="basic-addon1" 
                  value={data.sdt_zalo}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.sdt_zalo=e.target.value;
                    this.setState({data:data})
                  }}
                />
              </div>
            </div>
            <div className='col-8'>
              <div className="input-group mb-3">
                <span className="input-group-text">Lin h s in thoi Hotline:</span>
                <input type="text" className="form-control" placeholder="0933226771" aria-label="Username" aria-describedby="basic-addon1"
                  value={data.sdt_hotline}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.sdt_hotline=e.target.value;
                    this.setState({data:data})
                  }}
                />
              </div>
            </div>
            <div className='col-8'>
              <div className="input-group mb-3">
                <span className="input-group-text">Lin h facebook link:</span>
                <input type="text" className="form-control" placeholder="https://fb.me/xcxc" aria-label="Username" aria-describedby="basic-addon1" 
                  value={data.link_fb}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.link_fb=e.target.value;
                    this.setState({data:data})
                  }}
                />
              </div>
            </div>
            <div className='col-8'>
              <div className="input-group mb-3">
                <span className="input-group-text">Lin h a ch:</span>
                <input type="text" className="form-control" placeholder="S nh 19, ng s 17, quc l 13 c, Th c" aria-label="Username" aria-describedby="basic-addon1" 
                  value={data.dia_chi}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.dia_chi=e.target.value;
                    this.setState({data:data})
                  }}
                />
              </div>
            </div>
          </div>

          <div className='row mb-42'>
            * About footer:
            <div className='add-zz'
              onClick={()=>{
                let {data}=this.state;
                data.about_footer.push({
                  name:"",
                  link:""
                })
                this.setState({data:data})
              }}
            >
              <i className="fa-solid fa-circle-plus"
              ></i>
            </div>

            <div className='show-listz row'>
              {
                  data.about_footer.map((e,i)=>{
                    return (<div className="mb-3 input-group" key={i}>
                    <input placeholder="Tiu " className="form-control"
                      value={e.name}
                      onChange={(e)=>{
                        let {data}=this.state;
                        data.about_footer[i].name=e.target.value;
                        this.setState({data:data})
                      }}
                    />
                    <input placeholder="URL" className="form-control"
                        value={e.link}
                        onChange={(e)=>{
                          let {data}=this.state;
                          data.about_footer[i].link=e.target.value;
                          this.setState({data:data})
                        }}
                    />
                    <div className="icon-rm"
                      onClick={()=>{
                        if(window.confirm(`Bn c mun xa :"${e.name}"`)){
                          let {data}=this.state;
                          data.about_footer.splice(i,1);
                          this.setState({data:data})
                        }
                      }}
                    ><i className="fa-solid fa-circle-xmark"></i></div>
                  </div>)
                  })
                }
              </div>

            </div>
         

          <div className='row mb-42'>
            * Privacy footer:
            <div className='add-zz' 
              onClick={()=>{
                let {data}=this.state;
                data.privacy_footer.push({
                  name:"",
                  link:""
                })
                this.setState({data:data})
              }}
            >
              <i className="fa-solid fa-circle-plus"
              ></i>
            </div>

            <div className='show-listz row'>
              {
                data.privacy_footer.map((e,i)=>{
                  return (<div className="mb-3 input-group" key={i}>
                  <input placeholder="Tiu " className="form-control"
                    value={e.name}
                    onChange={(e)=>{
                      let {data}=this.state;
                      data.privacy_footer[i].name=e.target.value;
                      this.setState({data:data})
                    }}
                  />
                  <input placeholder="URL" className="form-control"
                      value={e.link}
                      onChange={(e)=>{
                        let {data}=this.state;
                        data.privacy_footer[i].link=e.target.value;
                        this.setState({data:data})
                      }}
                  />
                  <div className="icon-rm"
                    onClick={()=>{
                      if(window.confirm(`Bn c mun xa :"${e.name}"`)){
                        let {data}=this.state;
                        data.privacy_footer.splice(i,1);
                        this.setState({data:data})
                      }
                    }}
                  ><i className="fa-solid fa-circle-xmark"></i></div>
                </div>)
                })
              }

              </div>

            </div>
         

          <div className='row mb-42'>
            * Jobs footer:
            <div className='add-zz'
              onClick={()=>{
                let {data}=this.state;
                data.jobs_footer.push({
                  name:"",
                  link:""
                })
                this.setState({data:data})
              }}
            >
              <i className="fa-solid fa-circle-plus"
              ></i>
            </div>

            <div className='show-listz row'>
             {
                  data.jobs_footer.map((e,i)=>{
                    return (<div className="mb-3 input-group" key={i}>
                    <input placeholder="Tiu " className="form-control"
                      value={e.name}
                      onChange={(e)=>{
                        let {data}=this.state;
                        data.jobs_footer[i].name=e.target.value;
                        this.setState({data:data})
                      }}
                    />
                    <input placeholder="URL" className="form-control"
                        value={e.link}
                        onChange={(e)=>{
                          let {data}=this.state;
                          data.jobs_footer[i].link=e.target.value;
                          this.setState({data:data})
                        }}
                    />
                    <div className="icon-rm"
                      onClick={()=>{
                        if(window.confirm(`Bn c mun xa :"${e.name}"`)){
                          let {data}=this.state;
                          data.jobs_footer.splice(i,1);
                          this.setState({data:data})
                        }
                      }}
                    ><i className="fa-solid fa-circle-xmark"></i></div>
                  </div>)
                  })
                }
              </div>

            </div>

            <div className='row mb-42'>
              <div className='col-8'>
                <div className="input-group mb-3">
                  <span className="input-group-text">Cui trang, thit k bi:</span>
                  <input type="text" className="form-control" placeholder="Nest Art - Thnh Lp nm 2016. Thit k bi EPak Solutin." aria-label="Username" aria-describedby="basic-addon1" 
                     value={data.design_by}
                     onChange={(e)=>{
                       let {data}=this.state;
                       data.design_by=e.target.value;
                       this.setState({data:data})
                     }}
                  />
                </div>
              </div>
            </div>

            <div className='row mb-42'>
              <div className="form-floating">
                <textarea   className="form-control" style={{height: '200px'}}
                  value={data.code_header}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.code_header=e.target.value;
                    this.setState({data:data})
                  }}
                ></textarea>
                <label >Chn Code "header"  y</label>
              </div>
            </div>
            
            <div className='row mb-42'>
              <div className="form-floating">
                <textarea   className="form-control" style={{height: '200px'}}
                  value={data.code_body}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.code_body=e.target.value;
                    this.setState({data:data})
                  }}
                ></textarea>
                <label >Chn Code "body"  y</label>
              </div>
            </div>

            <div className='row mb-42'>
              <div className="form-floating">
                <textarea  className="form-control" style={{height: '200px'}}
                   value={data.code_footer}
                   onChange={(e)=>{
                     let {data}=this.state;
                     data.code_footer=e.target.value;
                     this.setState({data:data})
                   }}
                ></textarea>
                <label >Chn Code "footer"  y</label>
              </div>
            </div>
            <div className='row mb-42' style={{marginBottom:"200px"}}>
              * Chn ads Modal:
              <div className="form-floating">
                <textarea   className="form-control" style={{height: '200px'}}
                  value={data.ads_modal}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.ads_modal=e.target.value;
                    this.setState({data:data})
                  }}
                ></textarea>
                <label >Chn Code "ads"  y</label>
              </div>
              * Chn ads detail:
              <div className="form-floating">
                <textarea   className="form-control" style={{height: '200px'}}
                  value={data.ads_detail}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.ads_detail=e.target.value;
                    this.setState({data:data})
                  }}
                ></textarea>
                <label >Chn Code "ads"  y</label>
              </div>
              * Chn ads thm:
              <div className='add-zz' 
                onClick={()=>{
                  let {data}=this.state;
                  data.ads_more.push("")
                  this.setState({data:data})
                }}
              >
                <i className="fa-solid fa-circle-plus"
                ></i>
              </div>
              {
                data.ads_more.map((e,i)=>{
                  return(<div className="form-floating re">
                  <div className="icon-rm" style={{right:"0px"}}
                    onClick={()=>{
                      if(window.confirm("Bn c mun xa !")){
                        let {data}=this.state;
                      data.ads_more.splice(i,1);
                      this.setState({data:data})
                      }
                    }}
                  ><i className="fa-solid fa-circle-xmark"></i></div>
                  <textarea  className="form-control" style={{height: '100px'}}
                    value={e}
                    onChange={(e)=>{
                      let {data}=this.state;
                      data.ads_more[i]=e.target.value;
                      this.setState({data:data})
                    }}
                  ></textarea>
                  <label >ads {i}</label>
                </div>)
                })
              }
            </div>
          </div>
          <Media_img 
          is_open_media={is_open_media}
          handleClose={()=>this.setState({is_open_media:false})}
          fsReturn_media={(img_return)=>{
            let {img_ref,data}=this.state;
            switch(img_ref){
              case 'mini_icon':
                data.mini_icon=img_return.img;
                this.setState({data:data})
                break;
              case 'background_header':
                data.background_header=img_return.img;
                this.setState({data:data})
                break;
              case 'logo_website':
                data.logo_website=img_return.img;
                this.setState({data:data})
                break;
            }         
          }}
        />
        <div className="footer-edit">
            <button type="button" className="btn btn-primary"
              onClick={async()=>{
                let {data}=this.state;
                let a = await action_update_data_like_setup({
                  name:'setup_meme_theme',
                  value:JSON.stringify(data)
                })
                if(a.status){
                  toast.success(`Cp nht thnh cng.`,{theme: "colored"})
                }else{
                  toast.error('Li ri bn i!.',{theme: "colored"})
                }
              }}
            >Cp nht bi vit</button>
        </div>
      </React.Fragment>
    );
  }
}

