import React, { Component } from 'react';
import './catche.css'
import { toast } from 'react-toastify';
import {
  action_update_data_like_setup,
  get_data_like_setup,
  clear_catche_by_id
} from '../axios'
export default class Catche extends Component {
  constructor(props) {
    super(props)
      this.state = {
        data:{
          is_turn_cache:false,
          time_cache:0  
        }
      }
    }

  async componentDidMount() {
    let a= await get_data_like_setup('setup_cache_meme_theme')
    if(a.status){
      this.setState({data:a.data})
    }
  }

  render() {
    let {data}=this.state
    return (
      <React.Fragment>
        <div className='container mt-50'>

          <div className='row mb-42'>
            <div className='col-8'>
              <div className="form-check form-switch">
                <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefault"
                  checked={data.is_turn_cache}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.is_turn_cache=!data.is_turn_cache;
                    this.setState({data:data})
                  }}
                />
                <label className="form-check-label" for="flexSwitchCheckDefault">Bt cache cho trang web
                  ({!data.is_turn_cache&&<strong style={{color:'rosybrown'}}>Tt</strong>}
                  {data.is_turn_cache&&<strong style={{color:'green'}}>Bt</strong>})
                </label>
              </div>
            </div>
          </div>

          <div className='row mb-42'>
            <div className='col-8'>
              <div className="input-group mb-3">
                <span className="input-group-text">Thi gian cache (giy):</span>
                <input type="number" className="form-control" placeholder="60 ngha l 60 giy" aria-label="Username" aria-describedby="basic-addon1" 
                  value={data.time_cache}
                  onChange={(e)=>{
                    let {data}=this.state;
                    data.time_cache=e.target.value
                    this.setState({data:data})
                  }}
                />
              </div>
            </div>
          </div>

          {/* <div className='row mb-42'>
            <div className='col-8'>
              <div className="input-group mb-3" style={{position:"relative"}}>
                <span className="input-group-text">Xa cache trang home:</span>
                <span className='cleacs' style={{position:"absolute",left:"195px",top:"7px"}}
                  onClick={async()=>{
                    if(window.confirm(`Xc nhn xa catche Trang Ch (Home)`)){
                      let a =await clear_catche_by_id({
                        id:-1,
                        type:'home'
                      })
                      if(a.status){
                        toast.success(`Xa thnh cng.`,{theme: "colored"})
                      }else{
                        toast.error('Li ri bn i!.',{theme: "colored"})
                      }
                    }
                  }}
                >Clear Cache</span>
              </div>
            </div>
          </div> */}

          </div>
 
        <div className="footer-edit">
            <button type="button" className="btn btn-primary"
              onClick={async()=>{
                let {data}=this.state;
                let a = await action_update_data_like_setup({
                  name:'setup_cache_meme_theme',
                  value:JSON.stringify(data)
                })
                if(a.status){
                  toast.success(`Cp nht thnh cng.`,{theme: "colored"})
                }else{
                  toast.error('Li ri bn i!.',{theme: "colored"})
                }
              }}
            >Cp nht bi vit</button>
        </div>
      </React.Fragment>
    );
  }
}

